import { menu_items, v21 } from './renderdata';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
      maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
    },
    nested: {
      paddingLeft: theme.spacing(4),
    },
    nestedS: {
      paddingLeft: theme.spacing(8),
    },
  }),
);

export function listItem({value}){
  return (
    <div>

    </div>
  );
}

export default function NestedList({handleSelectedMenu, value}) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [openAA, setOpenAA] = React.useState(false);
  const [openAB, setOpenAB] = React.useState(false);
  const [openBA, setOpenBA] = React.useState(false);
  const [openCA, setOpenCA] = React.useState(false);

  const handleClickA = () => {
    setOpen(!open);
    setOpenAA(!openAA);
    setOpenAB(false);
    setOpenBA(false);
    setOpenCA(false);
  };
  const handleClickB = () => {
    setOpen(!open);
    setOpenAB(!openAB);
    setOpenAA(true);
    setOpenBA(false);
    setOpenCA(false);
  };
  const handleClickC = () => {
    setOpen(!open);
    setOpenBA(!openBA);
    setOpenAA(false);
    setOpenAB(false);
    setOpenCA(false);
  };
  const handleClickD = () => {
    setOpen(!open);
    setOpenCA(!openCA);
    setOpenAB(false);
    setOpenAA(false);
    setOpenBA(false);
  };

  const handleClickItems = () => {
    setOpen(!open);
    setOpenCA(!openCA);
    setOpenAB(false);
    setOpenAA(false);
    setOpenBA(false);
  };

  return (
    <div>
    
  {  value===0 && (
      <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      subheader={
        <ListSubheader component="div" id="nested-list-subheader">
          แฟ้มสะสมผลงาน
        </ListSubheader>
      }
      className={classes.root}
    >
      <ListItem button onClick={()=>{handleSelectedMenu(3)}}>
        <ListItemIcon>
          {menu_items[3].icon}
        </ListItemIcon>
        <ListItemText primary={menu_items[3].title} />
      </ListItem>
      <ListItem button onClick={()=>{handleSelectedMenu(4)}}>
      <ListItemIcon>
          {menu_items[4].icon}
        </ListItemIcon>
        <ListItemText primary={menu_items[4].title} />
      </ListItem>
      <ListItem button onClick={()=>{handleSelectedMenu(5)}}>
      <ListItemIcon>
          {menu_items[5].icon}
        </ListItemIcon>
        <ListItemText primary={menu_items[5].title} />
      </ListItem>
      <ListItem button onClick={()=>{handleSelectedMenu(6)}}>
      <ListItemIcon>
          {menu_items[6].icon}
        </ListItemIcon>
        <ListItemText primary={menu_items[6].title} />
      </ListItem>
      <ListItem button onClick={()=>{handleSelectedMenu(7)}}>
      <ListItemIcon>
          {menu_items[7].icon}
        </ListItemIcon>
        <ListItemText primary={menu_items[7].title} />
      </ListItem>
      </List>
    )}
</div>
  );
}